@import "~@/scss/global_var.scss";









































































































































































































































.EchartsPie{
    display: flex;
    justify-content: center;
}

.EchartsPie .card{
    border-radius: 25px;
    max-width: 460px;

    .title{
        position: sticky;
        top: 0;
        background: white;
        z-index: 1;
        color: $color-title;
    }

    .data-charts{
        overflow-y: auto;
        min-width: 400px;
        height: calc( 100vh - 240px );

        &::-webkit-scrollbar
        {
            width: 8px;
        }
    
        &::-webkit-scrollbar-thumb
        {
            border-radius: 10px;
            -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
            background-color: #d8d8d8;

            &:hover{
                background-color: #ecebeb;
            }
        }
    }
}
